.drawer-div {
    position: fixed;
    background-color: white;
    min-height: 100vh;
    border-top-right-radius: 17px;
    z-index: 100;
  }
  
  .drawer-opened {
    transition: 0.5s;
    left: 0px;
  }
  
  .drawer-closed {
    transition: 0.5s;
    left: -500px;
  }
  
  .close-icon:hover {
    cursor: pointer;
  }
  
  .close-icon {
    position: absolute;
    right: -5px;
    top: 10px
  }
  
  .drawer-open-button-div {
    position: fixed;
  
    background-color: #343a40;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  
    width: 105px;
    height: 35px;
    left: -60px;
    top: 80px;
  
    padding: 10px;
  
    display: flex;
    justify-content: flex-end;
  }
  
  .drawer-open-button-div:hover {
    cursor: pointer;
  }
  
  .drawer-header {
    margin-top: 35px;
  }
  